<template>
    <div>
        <pageCaption
            msg="Todo"
        ></pageCaption>
        <input v-model="addString" type="text" @keypress.enter="push"><button @click="push">add</button>
        <h2>not yet</h2>
        <hr>
        <ul>
            <li
                v-for="item in notYetList"
                is="todoItem"					
                :key="item.sn"
                :node="item"
            ></li>
        </ul>
        <h2>done</h2>
        <hr>
        <ul>
            <li
                v-for="item in doneList"
                is="todoItem"					
                :key="item.sn"
                :node="item"
            ></li>
        </ul>
    </div>
</template>

<script>
import pageCaption from '@/components/pageCaption.vue'
import todoItem from '@/components/todoItem.vue'

export default {
    components: {
        pageCaption,
        todoItem,  
    },
    data: function(){

        return {
            addString: '',
            todo: [
                {
                    sn: 1,
                    done: false,
                    name: 'buy a milk!',
                },
                {
                    sn: 2,
                    done: false,
                    name: 'buy a drink!',
                },
                {
                    sn: 3,
                    done: false,
                    name: 'buy a food!',
                },
                {
                    sn: 4,
                    done: true,
                    name: 'buy a egg!',
                },
                {
                    sn: 0,
                    done: true,
                    name: 'buy a baby!',
                },
            ],
        }
    },
    computed: {
        doneList: function(){
            
            return this.todo.filter(function(item){
                return item.done;
            });
        },
        notYetList: function(){
            
            return this.todo.filter(function(item){
                return !item.done;
            });
        },
    },
    methods: {
        push: function(){
            
            if( this.addString ) {
                
                this.todo.push({
                    sn: new Date().getTime(),
                    done: false,
                    name: this.addString,
                });
                
                this.addString = '';
            }
        },
        remove: function(index){
            this.man.splice(index, 1)
        },
    }
}
</script>

