        <ul>
            <li><button @click="changePage('todo')">to do</button></li>
            <li><button @click="changePage('about')">about me</button></li>
        </ul>
        <todo v-if="page === 'todo'"></todo>
        <about v-if="page === 'about'"></about>
    </div>
</template>

<script>
import todo from '@/pages/todo.vue'
import about from '@/pages/aboutMe.vue'

export default {
    name: 'App',
    components: {
        todo,
        about,
    },
    data: function(){

        return {
            page: 'todo',
        }
    },
    methods: {
        changePage: function(str) {
            this.page = str
        },
    }
}
</script>

